@media (min-width: 1200px) {
  * {
    text-decoration: none;
  }

  .home {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
  }

  /* HEADER////////////////////////////// */

  .header {
    width: 100%;
    display: flex;
    align-items: center;
    height: 60px;
    position: fixed;
    z-index: 100;
    justify-content: center;
    background: white;
    border-bottom: 2px solid rgba(227, 227, 227, 0.477);
  }

  .header-items {
    width: 90%;
    display: flex;
    align-items: center;
    height: 60px;
    justify-content: space-between;
  }

  .text-1 {
    font-family: Verdana, Times, serif;
    color: rgb(8, 7, 8);
    cursor: pointer;
  }

  .text-1 span {
    color: rgb(55, 144, 255);
  }

  .div-2 {
    width: 35%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-left: 30px;
  }

  .text-2 {
    color: rgb(55, 144, 255);
    font-weight: bold;
    font-family: Tahoma, Times, serif;
    transition: all 0.6s ease;
    border-bottom: 2px solid rgb(55, 144, 255);
  }

  .text-3 {
    font-family: Tahoma, Times, serif;
    color: gray;
  }

  .text-3:hover {
    border-bottom: 2px solid rgb(55, 144, 255);
  }

  .div-3 {
    display: flex;
    justify-content: space-between;
    width: 20%;
    align-items: center;
  }

  .img-1 {
    width: 30px;
    cursor: pointer;
  }

  .img-1:hover {
    opacity: 85%;
  }

  .img-1:active {
    opacity: 75%;
  }

  .img-2 {
    width: 30px;
    cursor: pointer;
    display: none;
  }

  .img-2:hover {
    opacity: 90%;
  }

  .btn-1 {
    color: rgb(55, 144, 255);
    margin-right: 10px;
  }

  .btn-1:hover {
    opacity: 90%;
  }

  .btn-2 {
    background-color: rgb(55, 144, 255);
    font-weight: bold;
    padding: 6px 20px;
    border-radius: 2px;
    color: white;
  }

  .btn-2:hover {
    opacity: 90%;
  }

  /* SECTION-1/////////////////////// */

  .section-1 {
    margin-top: 60px;
    display: flex;
    width: 100%;
    height: 600px;
  }

  .img-3 {
    width: 100%;
    height: 600px;
  }

  .div-4 {
    display: flex;
    width: 100%;
    position: absolute;
    height: 600px;
    flex-direction: column;
    align-items: center;
    justify-content: start;
  }

  .text-4 {
    font-family: Verdana, Times, serif;
    color: rgb(8, 7, 8);
    cursor: pointer;
    font-size: 30px;
  }

  .text-4 span {
    color: rgb(55, 144, 255);
  }

  .text-5 {
    font-size: 55px;
    width: 90%;
    color: black;
    text-align: center;
    font-family: "Arial Black", Times, serif;
    animation: slideLeft 1.5s ease-in;
  }

  @keyframes slideLeft {
    from {
      transform: translateX(-200px);
    }

    to {
      transform: translateX(0);
    }
  }

  .text-5 span {
    color: rgb(30, 33, 43);
  }

  .div-5 {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    margin-top: 40px;
  }

  .text-6 {
    color: rgb(78, 78, 78);
  }

  .btn-3 {
    background: rgb(82, 82, 140);
    padding: 10px 30px;
    border-radius: 20px;
    color: white;
    font-family: "Arial Black", Times, serif;
    margin-top: 30px;
    transition: all 0.6s ease;
  }

  .btn-3:hover {
    transform: translatey(-10px);
  }

  /* SECTION-2/////////////////////////// */

  .section-2 {
    display: flex;
    width: 100%;
    height: 500px;
    align-items: center;
    justify-content: start;
    flex-direction: column;
  }

  .div-6 {
    width: 90%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    margin-top: 30px;
  }

  .div-7 {
    width: 50%;
    display: flex;
    justify-content: start;
    align-items: start;
    flex-direction: column;
  }

  .div-8 {
    width: 50%;
    display: flex;
    justify-content: space-between;
    margin-right: 20px;
  }

  .div-9 {
    margin-left: 250px;
    margin-top: 20px;
  }

  .text-7 {
    margin-left: 10px;
  }

  .text-7 span {
    color: orange;
  }

  .img-4 {
    width: 80%;
    object-fit: cover;
  }

  .div-10 {
    margin-top: 15px;
    width: 100%;
    justify-content: start;
    align-items: center;
    display: flex;
  }

  .img-5 {
    width: 30px;
  }
}

